import { pdfjs } from "react-pdf/dist/esm/entry.webpack5";
import { useEffect } from "react";

export default function SelectPages(props) {
    const loadPages = async () => {
        const pdfDoc = await pdfjs.getDocument(
            {data: props.fileBuffer}
        ).promise;
        const container = document.getElementById("page-container");

        for (let i = 1; i <= pdfDoc.numPages; i++) {
            const canvasContainer = document.createElement("div");
            canvasContainer.setAttribute("id", "page-"+i);
            canvasContainer.setAttribute("class", "p-2 pb-0 bg-stone-200");

            const pageNum = document.createElement("p");
            pageNum.setAttribute("class", "prose prose-stone text-center");
            pageNum.innerHTML = i;

            const canvas = document.createElement("canvas");
            canvas.setAttribute("class", "w-40");

            canvasContainer.appendChild(canvas);
            canvasContainer.appendChild(pageNum);
            container.appendChild(canvasContainer);

            const page = await pdfDoc.getPage(i);
            const viewport =  page.getViewport({scale: canvas.width/page.getViewport({scale:1}).width});
            canvas.height = viewport.height;
            const render_context = {
                canvasContext: canvas.getContext('2d'),
                viewport: viewport
            };
            page.render(render_context);
        }
    };

    useEffect(() => {
        loadPages();
     });

    return (
        <>
            <h2 className="prose prose-stone text-center mx-auto mb-6 text-xl">Select pages to extract:</h2>
            <div id="page-container" className="grid grid-cols-2 md:grid-cols-4 gap-4 mx-auto"/>
        </>
    );
}